import React, { Component } from "react";
import "./ForgotPasswordStyle.css";

class ForgotPasswordForm extends Component {
  constructor() {
    super();
    this.state = {
      email: { name: "", message: "", op: "" },
    };
  }
  validateEmail = (event) => {
    let name = "email";
    let val = event.target.value;
    let email = this.state.email;
    email.name = val;
    this.setState(
      {
        email: email,
      },
      this.validateFunc(email, name, val)
    );
  };
  validateFunc(element, name, val) {
    let regex = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
    if (regex.test(val)) {
      element.op = "";
      element.message = "success";
    } else {
      element.message = "warn";
      element.op = `Email is not Valid `;
    }
    this.setState({
      email: element,
    });
  }
  submitData = () => {
    alert(`Your account is created Mr.${this.state.userName.name},`);
  };
  render() {
    const color = { color: "red", padding: "17px", fontSize: "18px" };
    const mark = <label style={color}>*</label>;
    let { email } = this.state;
    return (
      <div id="index">
        <div className="forgotMain">
          <h2 className="heading">Forgot Password</h2>
          <form className="regForm" id="regForm" onSubmit={this.submitData}>
            <div>
              <div className="inputBox">
                <input
                  type="text"
                  className="pwd"
                  id={email.message}
                  value={email.name}
                  onChange={this.validateEmail}
                  required
                />
                <span>Email{mark}</span>
              </div>
              <span className={email.message}>{email.op}</span>
            </div>
            <div>
              <div className="captchaClassForgot">
                <button type="submit" className="subbtn">
                  Verify
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default ForgotPasswordForm;
