import React, { Component } from "react";
import "./RegpageStyle.css";
import "./inputField.css";

class SignUpForm extends Component {
  constructor() {
    super();
    this.state = {
      userName: { name: "", message: "", op: "" },
      email: { name: "", message: "", op: "" },
      userId: { name: "", message: "", op: "" },
      password: { name: "", message: "", op: "" },
      confirmPassword: { name: "", message: "", op: "" },
    };
  }
  validateUsername = (event) => {
    let name = "userName";
    let val = event.target.value;
    let user = this.state.userName;
    user.name = val;
    this.setState(
      {
        userName: user,
      },
      () => {
        this.validateFunc(user, name, val);
      }
    );
  };
  validateEmail = (event) => {
    let name = "email";
    let val = event.target.value;
    let email = this.state.email;
    email.name = val;
    this.setState(
      {
        email: email,
      },
      this.validateFunc(email, name, val)
    );
  };
  validateUserID = (event) => {
    let name = "userId";
    let val = event.target.value;
    let id = this.state.userId;
    id.name = val;
    this.setState(
      {
        userId: id,
      },
      this.validateFunc(id, name, val)
    );
  };
  validatePassword = (event) => {
    let name = "password";
    let val = event.target.value;
    let pass = this.state.password;
    pass.name = val;
    console.log(val);
    this.setState(
      {
        password: pass,
      },
      this.validateFunc(pass, name, val)
    );
  };
  validateConfirmPassword = (event) => {
    let name = "confirm";
    let val = event.target.value;
    let confirm = this.state.confirmPassword;
    confirm.name = val;
    // console.log(val);
    this.setState(
      {
        confirmPassword: confirm,
      },
      this.validateFunc(confirm, name, val)
    );
  };
  validateFunc(element, name, val) {
    switch (name) {
      case "userName":
        if (val.length > 5) {
          if (val.length > 20) {
            element.op =
              "Name is too large(Length should be in between 6 and 20)";
            element.message = "warn";
          } else {
            element.message = "success";
            element.op = "";
          }
        } else {
          element.message = "warn";
          element.op = `Name is too short(Length should be in between 6 and 20) `;
        }
        this.setState({
          userName: element,
        });
        break;
      case "email":
        let regex = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
        if (regex.test(val)) {
          element.op = "";
          element.message = "success";
        } else {
          element.message = "warn";
          element.op = `Email is not Valid `;
        }
        this.setState({
          email: element,
        });
        break;
      case "userId":
        if (val.length > 4) {
          if (val.length > 10) {
            element.op =
              "Id is too large(Length should be in between 5 and 10)";
            element.message = "warn";
          } else {
            element.message = "success";
            element.op = "";
          }
        } else {
          element.message = "warn";
          element.op = `Id is too short(Length should be in between 5 and 10) `;
        }
        this.setState({
          userId: element,
        });
        break;
      case "password":
        let regexPassword =
          /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[^a-zA-Z0-9])(?!.*\s).{8,20}$/;
        if (regexPassword.test(val)) {
          element.message = "success";
        } else {
          element.message = "warn";
          element.op = `Should contain One Upper,Lower Case,Digit & Special Character`;
        }
        this.setState({
          password: element,
        });
        break;
      case "confirm":
        if (this.state.password.name === element.name) {
          element.message = "success";
          element.op = "";
        } else {
          element.message = "warn";
          element.op = "Password should match";
        }
        this.setState({
          confirmPassword: element,
        });
        break;
      default:
        break;
    }
  }
  submitData = () => {
    alert(`Your account is created Mr.${this.state.userName.name},`);
  };
  render() {
    const color = { color: "red", padding: "17px", fontSize: "18px" };
    const mark = <label style={color}>*</label>;
    // const userName = this.state.userName;
    let { userName, email, userId, password, confirmPassword } = this.state;
    // let s = userName.message;
    return (
      <div id="index">
        <div className="main">
          <h2 className="heading">RegistrationForm</h2>
          <form className="regForm" onSubmit={this.submitData}>
            <div>
              <div className="inputBox">
                <input
                  type="text"
                  // className="userInput"
                  id={userName.message}
                  value={userName.name}
                  onChange={this.validateUsername}
                  required
                />
                <span>User Name{mark}</span>
              </div>
              <span className={userName.message}>{userName.op}</span>
            </div>
            <div>
              <div className="inputBox">
                <input
                  type="text"
                  // className="userInput"
                  id={email.message}
                  value={email.name}
                  onChange={this.validateEmail}
                  required
                />
                <span>Email{mark}</span>
              </div>
              <span className={email.message}>{email.op}</span>
            </div>
            <div>
              <div className="inputBox">
                <input
                  type="text"
                  // className="userInput"
                  id={userId.message}
                  value={userId.name}
                  onChange={this.validateUserID}
                  required="required"
                />
                <span>User ID{mark}</span>
              </div>
              <span className={userId.message}>{userId.op}</span>
            </div>
            <div>
              <div className="inputBox">
                {/* <input type="password" id="hide" /> */}
                <input
                  type="password"
                  className="pwd"
                  id={password.message}
                  value={password.name}
                  onChange={this.validatePassword}
                  required
                />
                <span>Password{mark}</span>
              </div>
              <span className={password.message}>{password.op}</span>
            </div>
            <div>
              <div className="inputBox2">
                <input
                  type="password"
                  // className="userInput"
                  id={confirmPassword.message}
                  value={confirmPassword.name}
                  onChange={this.validateConfirmPassword}
                  required
                />
                <span>Confirm Password{mark}</span>
              </div>
              <span className={confirmPassword.message}>
                {confirmPassword.op}
              </span>
            </div>
            <div>
              <div className="captchaClass">
                <input type="checkbox" id="captcha" required />
                <label id="captchaText">Not a Robot</label>
                <button type="submit" className="subbtn">
                  Register
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default SignUpForm;
